@import "./vars.scss";
._header{
    .MenuBut{
        @media (min-width:500px) {
            display: none;
        }
    }
    z-index: 100;
    background: $pri;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    color: white;
    display: flex;
    padding: 0 80px;
    line-height: 60px;
    justify-content: space-between;
    @media (max-width:500px) {
        padding: 0 20px;
    }
    ._Name{
        font-size: 30px;
        font-weight: 900;
    }
    ._Menu{
        @media (max-width:500px) {
            display: none;
        }
        display: flex;
        &>a{
            text-decoration: none;
            color: inherit;
        }
        &>a>div{
            margin:0 10px;
            cursor: pointer;
            svg{
                margin-right: 5px;
            }
            &:hover{
                font-weight: 900;
                transition: .3s;
                line-height:60px;
                position: relative;
                &::after{
                    content: "";
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    width: 100%;
                    height: 5px;
                    border-radius: 10px 10px 0 0;
                    background: $th;
                    transition: .5s;
                }
            }
        }
        
    }
}