@import "./vars.scss";

._slide{
   .SlidImg{
       height: 100vh;
       .quat{
           font-size: 40px;
           color: white;
           font-weight: 900;
            padding: 216px 93px;
            text-shadow: black 2px 2px 2px;
       }
   }
}

._Discr{
    background: $pri;
    padding: 50px 0px;
    height:max-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
   
    .Disc{
        display: flex;
        color:white;
        font-size: 25px;
        align-self: center;
        i{
            width: 100%;
            display: flex;
            font-size: 200px;
            flex-direction: column;
            justify-content: center;
            margin-right:50px ;
        }
        
       
        
        width: 70%;
        

    }
}
._cards{
    padding: 150px 80px;
    &>div{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        
        justify-content: space-around;
        .card{
            width: 100%;
            display: flex;
            
            &:nth-child(even){
                justify-content: end;
            }
            &>.head{
                border-radius:30px;
                background: linear-gradient(145deg, #1c3b7a, #214790);
                box-shadow:  20px 20px 60px #1a3873, 
                 -20px -20px 60px #244c9b;
                height: 300px;
                min-width: 250px;
                margin: 20px;
                cursor: pointer;
                margin: 0 50px;
            }
            &>.cont{
                h1{
                    color: $pri;
                    font-weight: 900;
                    text-transform: uppercase;
                    margin: 10px 0;
                    text-decoration: underline;
                }
                width:40%;
                padding: 40px;
                color: white;
                font-weight:700;
                font-size: 20px;
            }
        }
        

    }
}
    
    


.ImgGal{
    background: white;
    padding: 50px 0px;
    h1{
        color: $pri;
    }
    ._img{
        height:350px;
        width: 300px;
        background: $f;
        cursor: pointer;
        background-position: center;
        background-size: cover;
    }
}